.weui-loadmore {
  width: 65%;
  margin: 1.5em auto;
  line-height: 1.6em;
  font-size: 14px;
  text-align: center;
}
.weui-loadmore__tips {
  display: inline-block;
  vertical-align: middle;
}
.weui-loadmore_line {
  border-top: 1px solid #E5E5E5;
  margin-top: 2.4em;
}
.weui-loadmore_line .weui-loadmore__tips {
  position: relative;
  top: -0.9em;
  padding: 0 .55em;
  color: #999999;
}
.weui-loadmore_dot .weui-loadmore__tips {
  padding: 0 .16em;
}
.weui-loadmore_dot .weui-loadmore__tips:before {
  content: " ";
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #E5E5E5;
  display: inline-block;
  position: relative;
  vertical-align: 0;
  top: -0.16em;
}
.vux-loadmore.weui-loadmore_line {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 0;
}
.vux-loadmore.weui-loadmore_line:before,
.vux-loadmore.weui-loadmore_line:after {
  position: relative;
  top: -1px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  content: '';
  border-top: 1px solid #E5E5E5;
}

.vux-marquee {
  width: 100%;
  overflow: hidden;
}
.vux-marquee-box {
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
}
.vux-marquee-box li {
  margin: 0;
  width: 100%;
}

.vux-masker-box {
  position: relative;
}
.vux-masker {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: inherit;
}
.vux-masker-fullscreen {
  position: fixed;
  z-index: 10001;
}

@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: "weui";
  src: url('data:application/octet-stream;base64,') format('truetype');
}
[class^="weui-icon-"],
[class*=" weui-icon-"] {
  display: inline-block;
  vertical-align: middle;
  font: normal normal normal 14px/1 "weui";
  font-size: inherit;
  text-rendering: auto;
